import React, { useEffect, useState } from "react";
import {
  Accordion,
  Button,
  Card,
  Col,
  Form,
  Modal,
  Row,
} from "react-bootstrap";
import IMAGES from "../../../../../assets/images";
import "react-datepicker/dist/react-datepicker.css";
import "react-time-picker/dist/TimePicker.css";
import { PATH } from "../../../../../utils/path";
import "moment/locale/en-gb";
import { useNavigate } from "react-router-dom";
import AppointmentDetail from "./AppointmentDetail";
import MedicalCertificate from "./MedicalCertificate";
import ClinicalNotes from "./ClinicalNotes";
import { useDispatch, useSelector } from "react-redux";
import {
  changeAppointmentStatus,
  getAllAppointment,
  getPatientAppointmentDetail,
} from "../../../../../app/features/appointment/appointment.slice";
import { getUser } from "../../../../../app/features/userManagement/user.slice";
import ReferralModule from "./Referral";
import PrescriptionModule from "./Prescription/index.jsx";
import moment from "moment";
import { showPatientPres } from "../../../../../app/features/patientPrescription/patientPrescription.slice";
import { sendAttachEmail } from "../../../../../app/features/patientPharmacy/patientPharmacy.slice";
import PatientPresModal from "./Prescription/PresModal";
import { useForm } from "react-hook-form";
import { IoIosArrowBack } from "react-icons/io";

export default function AppointmentNotes() {
  const user = JSON.parse(localStorage.getItem("family_doc_app"));
  const { register, handleSubmit } = useForm();

  const [presModal, setPresModal] = useState({
    data: null,
    show: false,
  });

  const [showEndModal, setShowEndModal] = useState(false);
  const [checkedApt, setCheckedApt] = useState(true);
  const [showPresToPatient, setShowPresToPatient] = useState(false);
  const [prescriptionIdToPatient, setPrescriptionIdToPatient] = useState();
  const [aptData, setAptData] = useState({});

  const [inputFieldValues, setInputFieldValues] = useState({
    presentingComp: "",
    presentingCompHistory: "",
    pastMedicalHistory: "",
    medications: "",
    allergies: "",
    familyHistory: "",
    socialHistory: "",
    suspectedDiagnosis: "",
    recommendedActions: "",
    signOff: "",
  });
  const { patientPharmacy } = useSelector((state) => state);

  const navigate = useNavigate();
  const { patientPharmacyData } = useSelector(
    (state) => state?.patientPharmacy
  );
  const { getAptDetails } = useSelector((state) => state?.appointmentDetails);

  const getAptNotesData = useSelector((state) => state);
  const handleCloseEndModal = () => {
    setShowEndModal(false);
  };
  const handleCheckedAptClick = (event) => {
    setCheckedApt(event.target.checked);
  };
  const slotDescription = JSON.parse(localStorage.getItem("slotDescription"));

  const dispatch = useDispatch();
  useEffect(() => {
    const finalData = {
      userId: user?.userId,
    };
    dispatch(getUser(finalData));
  }, [dispatch, user?.userId]);

  useEffect(() => {
    const finalData = {
      patientId: slotDescription?.patientId,
      appointmentId: slotDescription?.appointmentId,
    };
    dispatch(getPatientAppointmentDetail(finalData));
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [dispatch]);

  const moveToNext = () => {
    const finalData = {
      patientId: user?.userId,
      month: +moment(new Date()).format("M"),
    };
    dispatch(getAllAppointment(finalData));
    navigate(PATH.APPOINTMENT);
  };
  function appointmentStatus() {
    const finalData = {
      appointmentId: slotDescription?.appointmentId,
      statusId: 206,
    };
    dispatch(changeAppointmentStatus({ finalData, moveToNext }));
  }

  function presToPatientFun() {
    const finalData = {
      prescriptionId: prescriptionIdToPatient,
    };
    dispatch(showPatientPres({ finalData }));
  }

  function sendPrescToPharmacy(data) {
    const finalData = {
      email: patientPharmacyData?.pharmacyEmail
        ? patientPharmacyData?.pharmacyEmail
        : data?.pharmacyEmail,
      appointmentId: slotDescription?.appointmentId,
      patientFormStatusId: null,
    };
    dispatch(sendAttachEmail({ finalData, appointmentStatus }));
  }

  return (
    <>
      <div className="appointment-notes">
        <Row>
          <Col xs={12} className="appointment-notes-accordion px-0">
            <Form.Check
              type="checkbox"
              id="patient-attended-checkbox"
              label="Patient attended appointment"
              defaultChecked={
                getAptDetails?.data?.appointmentAttended === false || checkedApt
              }
              onChange={handleCheckedAptClick}
              className="mb-2"
              disabled={
                getAptDetails?.data?.failuretoattendnotes
                  ? true
                  : getAptDetails?.data?.appointmentAttended === true
                  ? true
                  : false
              }
            />

            <Card className="mb-3">
              <Card.Body className="mx-0 p-0">
                <Accordion defaultActiveKey="0">
                  <Accordion.Item eventKey="0">
                    <Accordion.Header>Appointment Detail</Accordion.Header>
                    <Accordion.Body className="py-4 px-5">
                      <AppointmentDetail
                        checkedApt={checkedApt}
                        setInputFieldValues={setInputFieldValues}
                        inputFieldValues={inputFieldValues}
                      />
                    </Accordion.Body>
                  </Accordion.Item>
                </Accordion>
              </Card.Body>
            </Card>
            {checkedApt && !getAptDetails?.data?.failuretoattendnotes && (
              <>
                <Card className="mb-3">
                  <Card.Body className="mx-0 p-0">
                    <Accordion>
                      <Accordion.Item eventKey="1">
                        <Accordion.Header>Notes to Patient</Accordion.Header>
                        <Accordion.Body className="mx-4">
                          <ClinicalNotes slotDescription={slotDescription} />
                        </Accordion.Body>
                      </Accordion.Item>
                    </Accordion>
                  </Card.Body>
                </Card>
                <Card className="mb-3">
                  <Card.Body className="mx-0 p-0">
                    <Accordion>
                      <Accordion.Item eventKey="1">
                        <Accordion.Header>
                          <img
                            src={IMAGES.PRESCRIPTION_ICON}
                            alt="prescription icon"
                            className="me-3 color-dk-blue"
                          />
                          Prescription
                        </Accordion.Header>
                        <Accordion.Body className="mx-4">
                          <PrescriptionModule
                            slotDescription={slotDescription}
                            showPresToPatient={showPresToPatient}
                            setShowPresToPatient={setShowPresToPatient}
                            setPrescriptionIdToPatient={
                              setPrescriptionIdToPatient
                            }
                          />
                        </Accordion.Body>
                      </Accordion.Item>
                    </Accordion>
                  </Card.Body>
                </Card>
                <Card className="mb-3">
                  <Card.Body className="mx-0 p-0">
                    <Accordion>
                      <Accordion.Item eventKey="2">
                        <Accordion.Header>
                          <img
                            src={IMAGES.REFERRAL_ICON}
                            alt="referral icon"
                            className="me-3 color-dk-blue"
                          />
                          Referral
                        </Accordion.Header>
                        <Accordion.Body className="py-4 px-5">
                          <ReferralModule
                            slotDescription={slotDescription}
                            aptData={aptData}
                            setAptData={setAptData}
                            inputFieldValues={
                              inputFieldValues
                                ? inputFieldValues
                                : getAptNotesData?.createAppointmentDetails
                                    ?.data
                            }
                            setInputFieldValues={setInputFieldValues}
                          />
                        </Accordion.Body>
                      </Accordion.Item>
                    </Accordion>
                  </Card.Body>
                </Card>
                <Card className="mb-3">
                  <Card.Body className="mx-0 p-0">
                    <Accordion>
                      <Accordion.Item eventKey="3">
                        <Accordion.Header>
                          <img
                            src={IMAGES.MEDICAL_CERTIFICATE_ICON}
                            alt="medical certificate icon"
                            className="me-3 color-dk-blue"
                          />
                          <span style={{ color: "#44BC19" }}>
                            Medical Certificate
                          </span>
                        </Accordion.Header>
                        <Accordion.Body className="py-4 px-5">
                          <MedicalCertificate
                            slotDescription={slotDescription}
                            aptData={aptData}
                            setAptData={setAptData}
                            inputFieldValues={
                              inputFieldValues
                                ? inputFieldValues
                                : getAptNotesData?.createAppointmentDetails
                                    ?.data
                            }
                            setInputFieldValues={setInputFieldValues}
                          />
                        </Accordion.Body>
                      </Accordion.Item>
                    </Accordion>
                  </Card.Body>
                </Card>
              </>
            )}

            <Button
              className="mt-4 start-apt-btn float-right px-5"
              onClick={() => {
                setShowEndModal(true);
                showPresToPatient === true && presToPatientFun();
              }}
            >
              Save appointment notes
            </Button>
          </Col>
        </Row>
        <Modal
          show={showEndModal}
          onHide={handleCloseEndModal}
          size="lg"
          aria-labelledby="contained-modal-title-vcenter"
          centered
          className="appointment-modal"
          backdrop="static"
        >
          <Modal.Header
            closeButton
            className="font-weight-600"
            style={{ fontSize: "18px" }}
          ></Modal.Header>
          <Modal.Body>
            <div className="d-flex justify-content-center flex-column mx-5 mb-3">
              {checkedApt === false ||
              getAptDetails?.data?.appointmentAttended === false ? (
                <>
                  <div className="d-flex justify-content-center ">
                    <img
                      src={IMAGES.NOTES_COMPLETED_ICON}
                      alt="Notes Completed Icon"
                    />
                  </div>
                  <div className="my-3">
                    <h3 className="text-center w-50 m-auto font-weight-600">
                      Notes Completed
                    </h3>
                    <p className="text-center mt-3 mb-4">
                      You have successfully completed appointments notes
                    </p>
                  </div>
                  <span className="d-flex justify-content-center">
                    <Button
                      className="start-apt-btn d-flex align-items-center"
                      onClick={() => appointmentStatus()}
                    >
                      <IoIosArrowBack
                        className="me-3"
                        size={20}
                        style={{ color: "#fff" }}
                      />
                      <p className="mb-0">Back to appointments</p>
                    </Button>
                  </span>
                </>
              ) : (
                // : PreMedicineData?.length <= 0 ||
                //   PreMedicineData === undefined ? (
                //   <>
                //     <p
                //       className="mb-0 font-weight-600 text-center"
                //       style={{ fontSize: "20px" }}
                //     >
                //       Please create Prescription to Complete the Appointment Notes
                //     </p>
                //     <div className="d-flex justify-content-center">
                //       <Button 
                //         className="start-apt-btn d-flex border-0 px-5 mt-5"
                //         onClick={() => {
                //           handleCloseEndModal();
                //           setPresModal({ data: null, show: true });
                //         }}
                //       >
                //         Create
                //       </Button>
                //     </div>
                //   </>
                // )
                <>
                  <Form onSubmit={handleSubmit(sendPrescToPharmacy)}>
                    <Form.Label
                      className="mb-4 font-weight-600"
                      style={{ fontSize: "18px" }}
                    >
                      Do you want to email this prescription to the pharmacy?
                    </Form.Label>
                    <div>
                      <p className="fw-bold m-0">Pharmcy Name:</p>
                      <p>
                        {patientPharmacy?.patientPharmacyData?.pharmacyName ??
                          "N/A"}
                      </p>
                    </div>
                    <div>
                      <p className="fw-bold m-0">Pharmcy Location:</p>
                      <p>
                        {patientPharmacy?.patientPharmacyData
                          ?.pharmacyLocation ?? "N/A"}
                      </p>
                    </div>
                    {patientPharmacy?.patientPharmacyData?.pharmacyEmail ? (
                      <div>
                        <p className="fw-bold m-0">Pharmcy Email:</p>
                        <p>
                          {patientPharmacy?.patientPharmacyData
                            ?.pharmacyEmail ?? "N/A"}
                        </p>
                      </div>
                    ) : (
                      <Form.Group>
                        <Form.Label className="fw-bold">
                          Pharmacy Email:
                        </Form.Label>
                        <Form.Control
                          type="email"
                          placeholder="Pharmacy Email"
                          {...register("pharmacyEmail")}
                          maxLength={250}
                        />
                      </Form.Group>
                    )}
                    {/* <Form.Group className="mt-4">
                      <Form.Control
                        type="email"
                        size="lg"
                        placeholder="Pharmacy Email"
                        {...register("pharmacyEmail")}
                        defaultValue={
                          patientPharmacy?.patientPharmacyData?.pharmacyEmail ??
                          ""
                        }
                        maxLength={250}
                        disabled={
                          patientPharmacy?.patientPharmacyData?.pharmacyEmail
                        }
                      />
                    </Form.Group> */}
                    <div className="mt-5 d-flex justify-content-center">
                      <Button
                        className="custom-outline-btn d-flex align-items-center me-4 font-weight-600"
                        onClick={() => appointmentStatus()}
                      >
                        <p className="mb-0">No</p>
                      </Button>
                      <Button
                        type="submit"
                        className="start-apt-btn d-flex align-items-center border-0"
                      >
                        Yes
                      </Button>
                    </div>
                  </Form>
                </>
              )}
            </div>
          </Modal.Body>
        </Modal>
      </div>
      {presModal && (
        <PatientPresModal
          show={presModal.show}
          onClose={() => setPresModal({ show: false, data: null })}
          slotDescription={slotDescription}
        />
      )}
    </>
  );
}
