import React, { useEffect, useRef, useState } from "react";
import {
  Button,
  Col,
  Dropdown,
  DropdownButton,
  Form,
  Row,
} from "react-bootstrap";
import { useForm } from "react-hook-form";
import { BsThreeDotsVertical } from "react-icons/bs";
import { FaEdit } from "react-icons/fa";
import { RiDeleteBin5Fill } from "react-icons/ri";
import { useDispatch, useSelector } from "react-redux";
import {
  createMedCertificate,
  deleteMedCertificate,
  getMedicalCertificate,
  updateMedCertificate,
} from "../../../../../../app/features/appointmentDetails/appointmentDetails.slice";
import { ConfirmationModal } from "../../../../../../components/Modal/ConfirmationModal";
import PrintComponents from "react-print-components";
import { PiDownloadSimpleBold } from "react-icons/pi";
import moment from "moment";
import MedicalCertificatePDF from "../../../../../../components/Theme/MedicalCertificatePDF";
import GenericDatePicker from "../../../../../../components/GenericDatePicker";

export default function MedicalCertificate() {
  const slotDescription = JSON.parse(localStorage.getItem("slotDescription"));

  const { register, handleSubmit, reset, setValue } = useForm();
  const [editing, setEditing] = useState(false);
  const printButtonRef = useRef(null);
  const [deleteId, setDeleteId] = useState();
  const [showMedCertificate, setShowMedCertificate] = useState(false);
  const [open, setOpen] = useState(false);

  const { getMediCertificate, isLoading, isSuccess, isError } = useSelector(
    (state) => state?.appointmentDetails
  );
  const [selectFromDate, setSelectFromDate] = useState(
    getMediCertificate?.fromDate
      ? new Date(getMediCertificate?.fromDate.split("T")[0])
      : null
  );
  const [selectToDate, setSelectToDate] = useState(
    getMediCertificate?.toDate
      ? new Date(getMediCertificate?.toDate.split("T")[0])
      : null
  );

  const { detailDTO } = useSelector(
    (state) => state?.appointment?.patientAppointedDetail || {}
  );
  const { getUserData } = useSelector((state) => state?.userRole);

  const dispatch = useDispatch();

  useEffect(() => {
    if (getMediCertificate) {
      setSelectFromDate(new Date(getMediCertificate.fromDate.split("T")[0]));
      setSelectToDate(new Date(getMediCertificate.toDate.split("T")[0]));
    } else {
      setSelectFromDate(null);
      setSelectToDate(null);
    }
  }, [getMediCertificate]);

  useEffect(() => {
    if (getMediCertificate?.unabletoAttendField) {
      setValue("unabletoAttendField", getMediCertificate?.unabletoAttendField);
    }
  }, [getMediCertificate?.unabletoAttendField, setValue]);

  useEffect(() => {
    const finalData = {
      appointmentId: slotDescription?.appointmentId,
    };
    dispatch(getMedicalCertificate(finalData));
  }, [dispatch, slotDescription?.appointmentId]);

  const onCreateSuccess = () => {
    const finalData = {
      appointmentId: slotDescription?.appointmentId,
    };
    dispatch(getMedicalCertificate(finalData));
    setEditing(false);
  };

  function deleteMedicalCertificate() {
    const finalData = {
      medicalcertificateId: deleteId,
    };
    dispatch(deleteMedCertificate({ finalData, onCreateSuccess }));
    reset();
  }
  function createMedicalCertificate(submittedData) {
    const finalData = {
      medicalCertificateId: getMediCertificate?.medicalCertificateId
        ? getMediCertificate?.medicalCertificateId
        : 0,
      appointmentId: slotDescription?.appointmentId,
      fromDate: selectFromDate
        ? moment(selectFromDate).format("YYYY-MM-DD")
        : null,
      toDate: selectToDate ? moment(selectToDate).format("YYYY-MM-DD") : null,
      name: submittedData.name,
      unabletoAttendField: submittedData.unabletoAttendField,
      pdfFile: null,
      sufferingFrom: submittedData?.name,
      showMedicalCertificate: showMedCertificate,
      isPatient: false,
    };
    debugger;
    if (getMediCertificate?.name) {
      dispatch(updateMedCertificate({ finalData, onCreateSuccess }));
    } else {
      dispatch(createMedCertificate({ finalData, onCreateSuccess }));
    }
  }

  return (
    <>
      {isLoading ? (
        <p
          className="my-5 text-center fst-italic"
          style={{
            color: "#999999",
          }}
        >
          Loading...
        </p>
      ) : isSuccess ? (
        <Form
          onSubmit={handleSubmit(createMedicalCertificate)}
          className="med-certificate"
        >
          <Row>
            <Col md={12} className="d-flex justify-content-between">
              <Form.Check
                type="checkbox"
                id="show-medical-certificate"
                label="Show to Patient"
                onChange={() => {
                  setShowMedCertificate(!showMedCertificate);
                }}
                disabled={
                  (getMediCertificate?.showMedicalCertificate === true &&
                    editing) ||
                  (getMediCertificate?.showMedicalCertificate === false &&
                    editing)
                    ? false
                    : getMediCertificate?.medicalCertificateId
                    ? true
                    : false
                }
                defaultChecked={
                  getMediCertificate?.showMedicalCertificate ?? ""
                }
              />
              {getMediCertificate?.name && editing === false && (
                <DropdownButton
                  id="dropdown-item-button"
                  title={
                    <BsThreeDotsVertical
                      className="float-right color-99"
                      size={25}
                    />
                  }
                  size="sm"
                  bsPrefix="moreIcon"
                  className="moreIcon edit-dropdown"
                  direction="left"
                >
                  <div className="set-dropdown-menu">
                    <Dropdown.Item
                      // as="button"
                      className="d-flex align-items-center"
                      onClick={() => {
                        setEditing(true);
                      }}
                    >
                      <FaEdit size={18} style={{ color: "#1A1A1A" }} />
                      <span
                        className="px-2"
                        style={{ fontSize: "14px", color: "#1A1A1A" }}
                      >
                        Edit
                      </span>
                    </Dropdown.Item>
                    <Dropdown.Item className="d-flex align-items-center">
                      <PrintComponents
                        trigger={
                          <button
                            ref={printButtonRef}
                            className="bg-transparent border-0 p-0"
                          >
                            <PiDownloadSimpleBold size={20} /> Download PDF
                          </button>
                        }
                      >
                        <MedicalCertificatePDF
                          MedicalCertificateData={getMediCertificate}
                          detailDTO={detailDTO}
                          getUserData={getUserData}
                        />
                      </PrintComponents>
                    </Dropdown.Item>
                    <Dropdown.Item
                      // as="button"
                      className="d-flex align-items-center"
                      onClick={() => {
                        setDeleteId(getMediCertificate?.medicalCertificateId);
                        setOpen(true);
                      }}
                    >
                      <RiDeleteBin5Fill
                        size={18}
                        style={{ color: "#FD2121" }}
                      />
                      <span
                        className="px-2"
                        style={{ fontSize: "14px", color: "#FD2121" }}
                      >
                        Remove
                      </span>
                    </Dropdown.Item>
                  </div>
                </DropdownButton>
              )}
            </Col>
            <Col lg={6} xs={12}>
              <label className="mb-2">From</label>
              <GenericDatePicker
                selectedDateState={selectFromDate}
                onChange={(date) => setSelectFromDate(date)}
                className="me-2"
                useDrDateClass={false}
                dateFormat="dd/MM/yyyy"
                placeholderText="day/month/year"
                disabled={
                  getMediCertificate?.fromDate && editing
                    ? false
                    : getMediCertificate?.fromDate
                    ? true
                    : false
                }
              />
            </Col>
            <Col lg={6} xs={12}>
              <label className="mb-2">To</label>
              <GenericDatePicker
                selectedDateState={selectToDate}
                onChange={(date) => setSelectToDate(date)}
                useDrDateClass={false}
                dateFormat="dd/MM/yyyy"
                placeholderText="day/month/year"
                disabled={
                  getMediCertificate?.toDate && editing
                    ? false
                    : getMediCertificate?.toDate
                    ? true
                    : false
                }
              />
              {/* <Form.Group controlId="to-date">
                <Form.Label>To</Form.Label>
                <Form.Control
                  type="date"
                  placeholderText="MM/DD/YYYY"
                  className="custom-date"
                  defaultValue={
                    getMediCertificate
                      ? moment(getMediCertificate?.toDate).format("YYYY-MM-DD")
                      : ""
                  }
                  disabled={
                    getMediCertificate?.toDate && editing
                      ? false
                      : getMediCertificate?.toDate
                      ? true
                      : false
                  }
                  name="toDate"
                  {...register("toDate", { required: true })}
                />
              </Form.Group> */}
            </Col>
            <Col xs={12}>
              <Form.Group className="my-3" controlId="unable-to-attend">
                <Form.Label>Unable to Attend</Form.Label>
                <Form.Select
                  aria-label="Select Unable to Attend"
                  {...register("unabletoAttendField", { required: true })}
                  disabled={
                    getMediCertificate?.unabletoAttendField && editing
                      ? false
                      : getMediCertificate?.unabletoAttendField
                      ? true
                      : false
                  }
                >
                  <option value="">Select Value</option>
                  <option value="work">Work</option>
                  <option value="school">School</option>
                  <option value="college">College</option>
                  <option value="jury duty">Jury Duty</option>
                </Form.Select>
              </Form.Group>
            </Col>
            <Col xs={12}>
              <Form.Group className="mb-3" controlId="suffering-from">
                <Form.Label className="label-primary">
                  Suffering From
                </Form.Label>
                <Form.Control
                  as="textarea"
                  rows={3}
                  className={`${
                    (editing || getMediCertificate?.name) && "bg-transparent"
                  }`}
                  placeholder="Tell about some medical certificate"
                  {...register("name", { required: true })}
                  defaultValue={getMediCertificate?.name ?? ""}
                  readOnly={
                    getMediCertificate?.name && editing
                      ? false
                      : getMediCertificate?.name
                      ? true
                      : false
                  }
                  maxLength={250}
                />
              </Form.Group>
            </Col>
            <Col xs={3} className="w-100">
              {(!getMediCertificate?.name || editing === true) && (
                <Button
                  className="mt-2 mb-3 start-apt-btn float-right"
                  type="submit"
                >
                  Save
                </Button>
              )}
            </Col>
          </Row>
        </Form>
      ) : isError ? (
        <p className="my-5 text-center text-danger fst-italic">Network Error</p>
      ) : null}
      <ConfirmationModal
        show={open}
        onHide={() => setOpen(false)}
        heading={"Delete Medical Certificate"}
        title={"this medical certificate"}
        removeFunc={deleteMedicalCertificate}
      />
    </>
  );
}
