import React, { useEffect, useRef } from "react";
import { useState } from "react";
import {
  Button,
  Col,
  Dropdown,
  DropdownButton,
  Form,
  Row,
} from "react-bootstrap";
import { useForm } from "react-hook-form";
import { BsThreeDotsVertical } from "react-icons/bs";
import { FaEdit } from "react-icons/fa";
import { RiDeleteBin5Fill } from "react-icons/ri";
import { useDispatch, useSelector } from "react-redux";
import PrintComponents from "react-print-components";
import { PiDownloadSimpleBold } from "react-icons/pi";

import {
  createPatientEmerRef,
  deletePatientEmerRef,
  updatePatientEmerRef,
} from "../../../../../../../app/features/patientRefNotes/patientRefNotes.slice";
import { getPatientAppointmentNotesDetail } from "../../../../../../../app/features/appointment/appointment.slice";
import EmergencyLetterPDF from "../../../../../../../components/Theme/EmergencyLetterPDF";
import { ConfirmationModal } from "../../../../../../../components/Modal/ConfirmationModal";

export default function EmergencyDptLetter({ inputFieldValues }) {
  const { register, handleSubmit, reset } = useForm();
  const slotDescription = JSON.parse(localStorage.getItem("slotDescription"));

  const [editing, setEditing] = useState(false);
  const [editingAptDetails, setEditingAptDetails] = useState(true);
  const [update, setUpdate] = useState(false);
  const [deleteId, setDeleteId] = useState();
  const [open, setOpen] = useState(false);
  const [isNeedToCopy, setIsNeedToCopy] = useState(false);
  const [showNotes, setShowNotes] = useState(false);

  const printButtonRef = useRef(null);
  const dispatch = useDispatch();

  const { data } = useSelector(
    (state) => state?.appointmentDetails?.getAptDetails || {}
  );

  const { emergancyDepartmentLetter } = useSelector(
    (state) => state?.appointment?.patientAppointmentNotes || {}
  );

  const { detailDTO } = useSelector(
    (state) => state?.appointment?.patientAppointedDetail || {}
  );
  const { getUserData } = useSelector((state) => state.userRole);

  const currentDate = new Date();
  const formattedDate = currentDate.toISOString().slice(0, 19);

  const onCreateSuccess = () => {
    const finalData = {
      appointmentId: slotDescription?.appointmentId,
    };
    dispatch(getPatientAppointmentNotesDetail(finalData));
    setEditing(false);
    setUpdate(false);
  };

  useEffect(() => {
    const finalData = {
      appointmentId: slotDescription?.appointmentId,
    };
    dispatch(getPatientAppointmentNotesDetail(finalData));
  }, [dispatch, slotDescription?.appointmentId]);

  const onCreateDeleteSuccess = () => {
    reset();
    const finalData = {
      appointmentId: slotDescription?.appointmentId,
    };
    dispatch(getPatientAppointmentNotesDetail(finalData));
    setIsNeedToCopy(false);
  };

  function deletePatientRefNotes() {
    const finalData = {
      noteId: deleteId,
    };
    dispatch(deletePatientEmerRef({ finalData, onCreateDeleteSuccess }));
    reset();
  }

  function createEmergencyRefNotes(submittedData) {
    const finalData = {
      noteId: emergancyDepartmentLetter?.noteId
        ? emergancyDepartmentLetter?.noteId
        : 0,
      noteTypeId: 2,
      patientId: slotDescription?.patientId,
      doctorId: slotDescription?.doctorId,
      referralNote: isNeedToCopy ? null : submittedData?.referralNote?.trim(),
      emergancyDepartmentLetter: submittedData.emergancyDepartmentLetter,
      referralTypeId: 2,
      toDoctor: submittedData.toDoctor,
      appointmentId: slotDescription?.appointmentId,
      showNote: showNotes,
      isPatient: false,
      date: formattedDate,
      appointmentDetail:
        isNeedToCopy ||
        emergancyDepartmentLetter?.appointmentDetail?.appointmentDetailId
          ? {
              appointmentDetailId: emergancyDepartmentLetter?.noteId
                ? emergancyDepartmentLetter?.appointmentDetail
                    ?.appointmentDetailId
                : 0,
              appointmentId: slotDescription?.appointmentId,
              presentingComplaint: submittedData?.presentingComplaint
                ? submittedData?.presentingComplaint
                : inputFieldValues?.presentingComplaint
                ? inputFieldValues?.presentingComplaint
                : data?.presentingComplaint,
              historyOfPresentingComplaint: submittedData?.presentingComplaint
                ? submittedData?.presentingComplaint
                : inputFieldValues?.presentingComplaint
                ? inputFieldValues?.presentingComplaint
                : data?.presentingComplaint,
              // historyOfPresentingComplaint:
              //   submittedData?.historyOfPresentingComplaint
              //     ? submittedData?.historyOfPresentingComplaint
              //     : inputFieldValues?.historyOfPresentingComplaint
              //     ? inputFieldValues?.historyOfPresentingComplaint
              //     : data?.historyOfPresentingComplaint,
              pastMedicalHistory: submittedData?.pastMedicalHistory
                ? submittedData?.pastMedicalHistory
                : inputFieldValues?.pastMedicalHistory
                ? inputFieldValues?.pastMedicalHistory
                : data?.pastMedicalHistory,
              medications: submittedData?.medications
                ? submittedData?.medications
                : inputFieldValues?.medications
                ? inputFieldValues?.medications
                : data?.medications,
              allergies: submittedData?.allergies
                ? submittedData?.allergies
                : inputFieldValues?.allergies
                ? inputFieldValues?.allergies
                : data?.allergies,
              familyHistory: submittedData?.familyHistory
                ? submittedData?.familyHistory
                : inputFieldValues?.familyHistory
                ? inputFieldValues?.familyHistory
                : data?.familyHistory,
              socialHistory: submittedData?.socialHistory
                ? submittedData?.socialHistory
                : inputFieldValues?.socialHistory
                ? inputFieldValues?.socialHistory
                : data?.socialHistory,
                objectiveExamination: submittedData?.objectiveExamination
                ? submittedData?.objectiveExamination
                : inputFieldValues?.objectiveExamination
                ? inputFieldValues?.objectiveExamination
                : data?.objectiveExamination,
              suspectedDiagnosis: submittedData?.suspectedDiagnosis
                ? submittedData?.suspectedDiagnosis
                : inputFieldValues?.suspectedDiagnosis
                ? inputFieldValues?.suspectedDiagnosis
                : data?.suspectedDiagnosis,
              recommendedActions: submittedData?.recommendedActions
                ? submittedData?.recommendedActions
                : inputFieldValues?.recommendedActions
                ? inputFieldValues?.recommendedActions
                : data?.recommendedActions,
              signOff: submittedData?.signOff
                ? submittedData?.signOff
                : inputFieldValues?.signOff
                ? inputFieldValues?.signOff
                : data?.signOff,
              appointmentAttended: true,
              failuretoattendnotes: "",
              referralTypeId: 2,
              noteId: emergancyDepartmentLetter?.noteId
                ? emergancyDepartmentLetter?.noteId
                : 0,
            }
          : emergancyDepartmentLetter?.noteId
          ? {
              appointmentDetailId: 0,
            }
          : null,
    };
    if (emergancyDepartmentLetter?.noteId) {
      dispatch(updatePatientEmerRef({ finalData, onCreateSuccess }));
    } else {
      dispatch(createPatientEmerRef({ finalData, onCreateSuccess }));
    }
    setEditingAptDetails(true);
  }

  return (
    <>
      <Form onSubmit={handleSubmit(createEmergencyRefNotes)}>
        <Row>
          <Col xs={12} className="mb-3">
            <div
              style={{ background: "#F8F8F8", borderRadius: "5px" }}
              className="px-3 py-3 h-100"
            >
              <span className="d-flex justify-content-between">
                <p className="referral-main-text" style={{ color: "#E31E27" }}>
                  Emergencey Department Letter
                </p>
                {emergancyDepartmentLetter?.patientId && update === false && (
                  <DropdownButton
                    id="dropdown-item-button"
                    title={
                      <BsThreeDotsVertical
                        className="float-right color-99"
                        size={25}
                      />
                    }
                    size="sm"
                    bsPrefix="moreIcon"
                    className="moreIcon edit-dropdown"
                    direction="left"
                  >
                    <div className="set-dropdown-menu">
                      <Dropdown.Item
                        // as="button"
                        className="d-flex align-items-center"
                      >
                        <FaEdit size={18} style={{ color: "#1A1A1A" }} />
                        <span
                          className="px-2"
                          style={{ fontSize: "14px", color: "#1A1A1A" }}
                          onClick={() => {
                            setEditing(true);
                            setUpdate(true);
                            setEditingAptDetails(false);
                          }}
                        >
                          Edit
                        </span>
                      </Dropdown.Item>
                      <Dropdown.Item className="d-flex align-items-center">
                        <PrintComponents
                          trigger={
                            <button
                              ref={printButtonRef}
                              className="bg-transparent border-0 p-0"
                            >
                              <PiDownloadSimpleBold size={20} /> Download PDF
                            </button>
                          }
                        >
                          <EmergencyLetterPDF
                            emergancyDepartmentLetter={
                              emergancyDepartmentLetter
                            }
                            detailDTO={detailDTO}
                            getUserData={getUserData}
                          />
                        </PrintComponents>
                      </Dropdown.Item>
                      <Dropdown.Item
                        // as="button"
                        className="d-flex align-items-center"
                        onClick={() => {
                          setDeleteId(emergancyDepartmentLetter?.noteId);
                          setOpen(true);
                        }}
                      >
                        <RiDeleteBin5Fill
                          size={18}
                          style={{ color: "#FD2121" }}
                        />
                        <span
                          className="px-2"
                          style={{ fontSize: "14px", color: "#FD2121" }}
                        >
                          Remove
                        </span>
                      </Dropdown.Item>
                    </div>
                  </DropdownButton>
                )}
              </span>
              <Form.Check
                type="checkbox"
                id="show-emergency-notes"
                label="Show to Patient"
                onChange={() => setShowNotes(!showNotes)}
                disabled={
                  (emergancyDepartmentLetter?.showNote === true && update) ||
                  (emergancyDepartmentLetter?.showNote === false && update)
                    ? false
                    : emergancyDepartmentLetter?.noteId
                    ? true
                    : false
                }
                defaultChecked={emergancyDepartmentLetter?.showNote}
              />
              <Form.Group className="mb-3" controlId="formBasicLabel3">
                <Form.Label className="label-primary mb-0">
                  Refferal Letter
                </Form.Label>
                <Form.Control
                  type="text"
                  placeholder="Tiredness"
                  className={`pt-2 custom-field-picker ${
                    editing && "bg-white"
                  }`}
                  {...register("emergancyDepartmentLetter", { required: true })}
                  defaultValue={
                    emergancyDepartmentLetter?.emergancyDepartmentLetter ?? ""
                  }
                  readOnly={
                    emergancyDepartmentLetter?.emergancyDepartmentLetter &&
                    update
                      ? false
                      : emergancyDepartmentLetter?.emergancyDepartmentLetter
                      ? true
                      : false
                  }
                  maxLength={250}
                />
              </Form.Group>
              <Form.Group className="mb-3" controlId="formBasicLabel3">
                <Form.Label className="label-primary mb-0">To</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="Specialist name"
                  className={`pt-2 custom-field-picker ${
                    editing && "bg-white"
                  }`}
                  {...register("toDoctor", { required: true })}
                  defaultValue={emergancyDepartmentLetter?.toDoctor ?? ""}
                  readOnly={
                    emergancyDepartmentLetter?.toDoctor && update
                      ? false
                      : emergancyDepartmentLetter?.toDoctor
                      ? true
                      : false
                  }
                  maxLength={250}
                />
              </Form.Group>
              <Form.Group
                className="mb-2"
                style={{ fontSize: "18px", fontWeight: 500 }}
                controlId="referralNote"
              >
                <Form.Label className="mb-0">Referral Notes</Form.Label>
                {isNeedToCopy === false &&
                !emergancyDepartmentLetter?.appointmentDetail ? (
                  <Form.Control
                    as="textarea"
                    rows={2}
                    placeholder="Referral Note"
                    className={`pt-2 custom-field-picker ${
                      editing && "bg-white"
                    }`}
                    {...register("referralNote", { required: true })}
                    defaultValue={emergancyDepartmentLetter?.referralNote ?? ""}
                    readOnly={
                      emergancyDepartmentLetter?.referralNote && update
                        ? false
                        : emergancyDepartmentLetter?.referralNote
                        ? true
                        : false
                    }
                    maxLength={250}
                  />
                ) : (
                  ""
                )}
              </Form.Group>
              {(isNeedToCopy ||
                emergancyDepartmentLetter?.appointmentDetail) && (
                <>
                  <Form.Group
                    className="mb-2"
                    style={{ fontSize: "18px", fontWeight: 500 }}
                    controlId="presentingComplaint"
                  >
                    <Form.Label className="mb-0">
                      Presenting Complaint
                    </Form.Label>
                    <Form.Control
                      as="textarea"
                      rows={2}
                      className={`pt-2 custom-field-picker ${
                        editingAptDetails && "bg-white"
                      }`}
                      {...register("presentingComplaint")}
                      defaultValue={
                        inputFieldValues?.presentingComplaint
                          ? inputFieldValues?.presentingComplaint
                          : data?.presentingComplaint
                          ? data?.presentingComplaint
                          : emergancyDepartmentLetter?.appointmentDetail
                              ?.presentingComplaint
                          ? emergancyDepartmentLetter?.appointmentDetail
                              ?.presentingComplaint
                          : "N/A"
                      }
                      disabled={editingAptDetails}
                    />
                  </Form.Group>

                  {/* <Form.Group
                    className="mb-2"
                    style={{ fontSize: "18px", fontWeight: 500 }}
                    controlId="exampleForm.ControlTextarea1"
                  >
                    <Form.Label className="mb-0">
                      History of Presenting Complaint
                    </Form.Label>
                    <Form.Control
                      as="textarea"
                      rows={2}
                      className={`pt-2 custom-field-picker ${
                        editingAptDetails && "bg-white"
                      }`}
                      {...register("historyOfPresentingComplaint")}
                      defaultValue={
                        inputFieldValues?.historyOfPresentingComplaint
                          ? inputFieldValues?.historyOfPresentingComplaint
                          : emergancyDepartmentLetter?.appointmentDetail
                              ?.historyOfPresentingComplaint
                          ? emergancyDepartmentLetter?.appointmentDetail
                              ?.historyOfPresentingComplaint
                          : data?.historyOfPresentingComplaint
                          ? data?.historyOfPresentingComplaint
                          : "N/A"
                      }
                      disabled={editingAptDetails}
                    />
                  </Form.Group> */}

                  <Form.Group
                    className="mb-2"
                    style={{ fontSize: "18px", fontWeight: 500 }}
                    controlId="pastMedicalHistory"
                  >
                    <Form.Label className="mb-0">
                      Past Medical History
                    </Form.Label>
                    <Form.Control
                      as="textarea"
                      rows={2}
                      className={`pt-2 custom-field-picker ${
                        editingAptDetails && "bg-white"
                      }`}
                      {...register("pastMedicalHistory")}
                      defaultValue={
                        inputFieldValues?.pastMedicalHistory
                          ? inputFieldValues?.pastMedicalHistory
                          : emergancyDepartmentLetter?.appointmentDetail
                              ?.pastMedicalHistory
                          ? emergancyDepartmentLetter?.appointmentDetail
                              ?.pastMedicalHistory
                          : data?.pastMedicalHistory
                          ? data?.pastMedicalHistory
                          : "N/A"
                      }
                      disabled={editingAptDetails}
                    />
                  </Form.Group>

                  <Form.Group
                    className="mb-2"
                    style={{ fontSize: "18px", fontWeight: 500 }}
                    controlId="medications"
                  >
                    <Form.Label className="mb-0">Medications</Form.Label>
                    <Form.Control
                      as="textarea"
                      rows={2}
                      className={`pt-2 custom-field-picker ${
                        editingAptDetails && "bg-white"
                      }`}
                      {...register("medications")}
                      defaultValue={
                        inputFieldValues?.medications
                          ? inputFieldValues?.medications
                          : emergancyDepartmentLetter?.appointmentDetail
                              ?.medications
                          ? emergancyDepartmentLetter?.appointmentDetail
                              ?.medications
                          : data?.medications
                          ? data?.medications
                          : "N/A"
                      }
                      disabled={editingAptDetails}
                    />
                  </Form.Group>

                  <Form.Group
                    className="mb-2"
                    style={{ fontSize: "18px", fontWeight: 500 }}
                    controlId="allergies"
                  >
                    <Form.Label className="mb-0">Allergies</Form.Label>
                    <Form.Control
                      as="textarea"
                      rows={2}
                      className={`pt-2 custom-field-picker ${
                        editingAptDetails && "bg-white"
                      }`}
                      {...register("allergies")}
                      defaultValue={
                        inputFieldValues?.allergies
                          ? inputFieldValues?.allergies
                          : emergancyDepartmentLetter?.appointmentDetail
                              ?.allergies
                          ? emergancyDepartmentLetter?.appointmentDetail
                              ?.allergies
                          : data?.allergies
                          ? data?.allergies
                          : "N/A"
                      }
                      disabled={editingAptDetails}
                    />
                  </Form.Group>

                  <Form.Group
                    className="mb-2"
                    style={{ fontSize: "18px", fontWeight: 500 }}
                    controlId="familyHistory"
                  >
                    <Form.Label className="mb-0">Family History</Form.Label>
                    <Form.Control
                      as="textarea"
                      rows={2}
                      className={`pt-2 custom-field-picker ${
                        editingAptDetails && "bg-white"
                      }`}
                      {...register("familyHistory")}
                      defaultValue={
                        inputFieldValues?.familyHistory
                          ? inputFieldValues?.familyHistory
                          : emergancyDepartmentLetter?.appointmentDetail
                              ?.familyHistory
                          ? emergancyDepartmentLetter?.appointmentDetail
                              ?.familyHistory
                          : data?.familyHistory
                          ? data?.familyHistory
                          : "N/A"
                      }
                      disabled={editingAptDetails}
                    />
                  </Form.Group>

                  <Form.Group
                    className="mb-2"
                    style={{ fontSize: "18px", fontWeight: 500 }}
                    controlId="socialHistory"
                  >
                    <Form.Label className="mb-0">Social History</Form.Label>
                    <Form.Control
                      as="textarea"
                      rows={2}
                      className={`pt-2 custom-field-picker ${
                        editingAptDetails && "bg-white"
                      }`}
                      {...register("socialHistory")}
                      defaultValue={
                        inputFieldValues?.socialHistory
                          ? inputFieldValues?.socialHistory
                          : emergancyDepartmentLetter?.appointmentDetail
                              ?.socialHistory
                          ? emergancyDepartmentLetter?.appointmentDetail
                              ?.socialHistory
                          : data?.socialHistory
                          ? data?.socialHistory
                          : "N/A"
                      }
                      disabled={editingAptDetails}
                    />
                  </Form.Group>

                  <Form.Group
                    className="mb-2"
                    style={{ fontSize: "18px", fontWeight: 500 }}
                    controlId="objectiveExamination"
                  >
                    <Form.Label className="mb-0">
                    Objective Examination
                    </Form.Label>
                    <Form.Control
                      as="textarea"
                      rows={2}
                      className={`pt-2 custom-field-picker ${
                        editingAptDetails && "bg-white"
                      }`}
                      {...register("objectiveExamination")}
                      defaultValue={
                        inputFieldValues?.objectiveExamination
                          ? inputFieldValues?.objectiveExamination
                          : emergancyDepartmentLetter?.appointmentDetail
                              ?.objectiveExamination
                          ? emergancyDepartmentLetter?.appointmentDetail
                              ?.objectiveExamination
                          : data?.objectiveExamination
                          ? data?.objectiveExamination
                          : "N/A"
                      }
                      disabled={editingAptDetails}
                    />
                  </Form.Group>

                  <Form.Group
                    className="mb-2"
                    style={{ fontSize: "18px", fontWeight: 500 }}
                    controlId="suspectedDiagnosis"
                  >
                    <Form.Label className="mb-0">
                      Suspected Diagnosis
                    </Form.Label>
                    <Form.Control
                      as="textarea"
                      rows={2}
                      className={`pt-2 custom-field-picker ${
                        editingAptDetails && "bg-white"
                      }`}
                      {...register("suspectedDiagnosis")}
                      defaultValue={
                        inputFieldValues?.suspectedDiagnosis
                          ? inputFieldValues?.suspectedDiagnosis
                          : emergancyDepartmentLetter?.appointmentDetail
                              ?.suspectedDiagnosis
                          ? emergancyDepartmentLetter?.appointmentDetail
                              ?.suspectedDiagnosis
                          : data?.suspectedDiagnosis
                          ? data?.suspectedDiagnosis
                          : "N/A"
                      }
                      disabled={editingAptDetails}
                    />
                  </Form.Group>

                  <Form.Group
                    className="mb-2"
                    style={{ fontSize: "18px", fontWeight: 500 }}
                    controlId="recommendedActions"
                  >
                    <Form.Label className="mb-0">
                      Recommended Actions
                    </Form.Label>
                    <Form.Control
                      as="textarea"
                      rows={2}
                      className={`pt-2 custom-field-picker ${
                        editingAptDetails && "bg-white"
                      }`}
                      {...register("recommendedActions")}
                      defaultValue={
                        inputFieldValues?.recommendedActions
                          ? inputFieldValues?.recommendedActions
                          : emergancyDepartmentLetter?.appointmentDetail
                              ?.recommendedActions
                          ? emergancyDepartmentLetter?.appointmentDetail
                              ?.recommendedActions
                          : data?.recommendedActions
                          ? data?.recommendedActions
                          : "N/A"
                      }
                      disabled={editingAptDetails}
                    />
                  </Form.Group>

                  <Form.Group
                    className="mb-2"
                    style={{ fontSize: "18px", fontWeight: 500 }}
                    controlId="signOff"
                  >
                    <Form.Label className="mb-0">Sign off</Form.Label>
                    <Form.Control
                      as="textarea"
                      rows={2}
                      className={`pt-2 custom-field-picker ${
                        editingAptDetails && "bg-white"
                      }`}
                      {...register("signOff")}
                      defaultValue={
                        inputFieldValues?.signOff
                          ? inputFieldValues?.signOff
                          : emergancyDepartmentLetter?.appointmentDetail
                              ?.signOff
                          ? emergancyDepartmentLetter?.appointmentDetail
                              ?.signOff
                          : data?.signOff
                          ? data?.signOff
                          : "N/A"
                      }
                      disabled={editingAptDetails}
                    />
                  </Form.Group>
                </>
              )}

              {!emergancyDepartmentLetter?.appointmentDetail && (
                <div className="d-flex justify-content-end">
                  <p
                    className="float-right mb-0"
                    style={{
                      color: "#2269F2",
                      fontWeght: 500,
                      fontSize: "18px",
                      cursor: "pointer",
                    }}
                    onClick={() => setIsNeedToCopy(true)}
                  >
                    Generate letter with appointment notes
                  </p>
                </div>
              )}
              {!emergancyDepartmentLetter?.patientId && (
                <div className="d-flex justify-content-end">
                  <Button className="start-apt-btn" type="submit">
                    Save
                  </Button>
                </div>
              )}
              {emergancyDepartmentLetter?.patientId && update === true && (
                <Button
                  className="mt-2 mb-3 start-apt-btn float-right"
                  type="submit"
                >
                  Update
                </Button>
              )}
            </div>
          </Col>
        </Row>
      </Form>
      <ConfirmationModal
        show={open}
        onHide={() => setOpen(false)}
        heading={"Delete Referral Notes"}
        title={"this refferal notes"}
        removeFunc={deletePatientRefNotes}
      />
    </>
  );
}
