import { Button, Modal } from "react-bootstrap";

export default function CheckPatientModal({ ...rest }) {
  const { show, handleClose } = rest;

  return (
    <Modal
      show={show}
      onHide={handleClose}
      size="lg"
      aria-labelledby="patient-modal"
      centered
      className="appointment-modal"
    >
      <Modal.Body>
        <div className="d-flex justify-content-center flex-column align-items-center text-center p-3">
          <p className="fw-bold">
            Tick the box if you have checked the patient's allergy to any
            medication
          </p>

          <div className="d-flex justify-content-center">
            <Button
              className="block primary_bg px-4"
              variant="primary"
              type="button"
              onClick={handleClose}
            >
              OK
            </Button>
          </div>
        </div>
      </Modal.Body>
    </Modal>
  );
}
