import React, { useEffect, useState } from "react";
import { Button, Dropdown, DropdownButton, Form } from "react-bootstrap";
import { useForm } from "react-hook-form";
import { BsThreeDotsVertical } from "react-icons/bs";
import { FaEdit } from "react-icons/fa";
import { RiDeleteBin5Fill } from "react-icons/ri";
import { useDispatch, useSelector } from "react-redux";
import { ConfirmationModal } from "../../../../../components/Modal/ConfirmationModal";
import {
  createPatientNotes,
  deletePatientNotes,
  updatePatientNotes,
} from "../../../../../app/features/patientNotes/patientNotes.slice";
import { getPatientAppointmentNotesDetail } from "../../../../../app/features/appointment/appointment.slice";

export default function ClinicalNotes() {
  const slotDescription = JSON.parse(localStorage.getItem("slotDescription"));

  const { register, handleSubmit, reset } = useForm();
  const [deleteId, setDeleteId] = useState();
  const [open, setOpen] = useState(false);
  const [editing, setEditing] = useState(false);

  const { patientAppointmentNotes, isLoading, isError, isSuccess } =
    useSelector((state) => state?.appointment || {});

  const dispatch = useDispatch();
  const currentDate = new Date();
  const formattedDate = currentDate.toISOString().slice(0, 19);

  useEffect(() => {
    const finalData = {
      appointmentId: slotDescription?.appointmentId,
    };
    dispatch(getPatientAppointmentNotesDetail(finalData));
  }, [dispatch, slotDescription?.appointmentId]);

  const moveToNext = () => {
    const finalData = {
      appointmentId: slotDescription?.appointmentId,
    };
    dispatch(getPatientAppointmentNotesDetail(finalData));
    setEditing(false);
    reset();
  };

  function deletePatientRefNotes() {
    const finalData = {
      noteId: deleteId,
    };
    dispatch(deletePatientNotes({ finalData, moveToNext }));
  }

  function createPatientRefNotes(submittedData) {
    const finalData = {
      noteId: patientAppointmentNotes?.note?.noteId
        ? patientAppointmentNotes?.note?.noteId
        : 0,
      patientId: slotDescription?.patientId,
      doctorId: slotDescription?.doctorId,
      appointmentId: slotDescription?.appointmentId,
      noteTypeId: 1,
      note: submittedData?.note,
      date: formattedDate,
      showNote: true,
      isPatient: false,
      appointmentDetail: patientAppointmentNotes?.note?.note
        ? {
            appointmentDetailId: 0,
          }
        : null,
    };
    if (patientAppointmentNotes?.note?.note) {
      dispatch(updatePatientNotes({ finalData, moveToNext }));
    } else {
      dispatch(createPatientNotes({ finalData, moveToNext }));
    }
  }

  return (
    <>
      {isLoading ? (
        <p
          className="my-5 text-center fst-italic"
          style={{
            color: "#999999",
          }}
        >
          Loading...
        </p>
      ) : isSuccess ? (
        <Form onSubmit={handleSubmit(createPatientRefNotes)}>
          <Form.Group className="mb-3" controlId="medicalForm.ControlTextarea1">
            <span className="d-flex justify-content-between">
              <Form.Control
                as="textarea"
                rows={3}
                className={`${
                  (editing || patientAppointmentNotes?.note?.note) &&
                  "bg-transparent"
                }`}
                placeholder="Tell about Notes to Patient"
                {...register("note", { required: true })}
                defaultValue={patientAppointmentNotes?.note?.note || ""}
                readOnly={
                  patientAppointmentNotes?.note?.note && editing
                    ? false
                    : patientAppointmentNotes?.note?.note
                    ? true
                    : false
                }
              />

              {patientAppointmentNotes?.note?.note && editing === false && (
                <DropdownButton
                  id="dropdown-item-button"
                  title={
                    <BsThreeDotsVertical
                      className="float-right color-99"
                      size={25}
                    />
                  }
                  size="sm"
                  bsPrefix="moreIcon"
                  className="moreIcon edit-dropdown"
                  direction="left"
                >
                  <div className="set-dropdown-menu">
                    <Dropdown.Item
                      // as="button"
                      className="d-flex align-items-center"
                      onClick={() => {
                        setEditing(true);
                      }}
                    >
                      <FaEdit size={18} style={{ color: "#1A1A1A" }} />
                      <span
                        className="px-2"
                        style={{ fontSize: "14px", color: "#1A1A1A" }}
                      >
                        Edit
                      </span>
                    </Dropdown.Item>
                    <Dropdown.Item
                      // as="button"
                      className="d-flex align-items-center"
                      onClick={() => {
                        setDeleteId(patientAppointmentNotes?.note?.noteId);
                        setOpen(true);
                      }}
                    >
                      <RiDeleteBin5Fill
                        size={18}
                        style={{ color: "#FD2121" }}
                      />
                      <span
                        className="px-2"
                        style={{ fontSize: "14px", color: "#FD2121" }}
                      >
                        Remove
                      </span>
                    </Dropdown.Item>
                  </div>
                </DropdownButton>
              )}
            </span>
          </Form.Group>

          {(!patientAppointmentNotes?.note?.note || editing === true) && (
            <Button
              className="mt-2 mb-3 start-apt-btn float-right"
              type="submit"
            >
              Save
            </Button>
          )}
        </Form>
      ) : isError ? (
        <p className="my-5 text-center text-danger fst-italic">Network Error</p>
      ) : null}
      <ConfirmationModal
        show={open}
        onHide={() => setOpen(false)}
        heading={"Delete Notes to Patient"}
        title={"this notes to patient"}
        removeFunc={deletePatientRefNotes}
      />
    </>
  );
}
